/* Custom styles for AI Infrastructure Agent - Based on Original Web UI */
.App {
  height: 100%;
}

/* Reset and Base Styles - Matching Original Web UI */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Ensure full height layout with original styling */
html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  color: #333;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Layout Adjustments */
.ant-layout {
  background: transparent;
}

.ant-layout-content {
  background: #f0f2f5;
}

/* Infrastructure State Styles - Modern UX Design */
.resource-card {
  border: 1px solid #e8e8e8;
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
  background: #ffffff;
}

.resource-card:hover {
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
  border-color: #d9d9d9;
  transform: translateY(-2px);
}

.resource-header {
  position: relative;
}

.resource-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  background: #f8f9fa;
  border-radius: 12px;
  border: 1px solid #e8e8e8;
  transition: all 0.2s ease;
}

.resource-card:hover .resource-icon {
  background: #f0f7ff;
  border-color: #1890ff;
}

.properties-container {
  background-color: #fafafa;
  border-radius: 8px;
  border: 1px solid #f0f0f0;
  max-height: 300px;
  overflow: auto;
  transition: all 0.2s ease;
}

.properties-container:hover {
  border-color: #d9d9d9;
}

/* Custom scrollbar for properties */
.properties-container::-webkit-scrollbar {
  width: 6px;
}

.properties-container::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 3px;
}

.properties-container::-webkit-scrollbar-thumb {
  background: #d9d9d9;
  border-radius: 3px;
}

.properties-container::-webkit-scrollbar-thumb:hover {
  background: #bfbfbf;
}

/* Responsive grid adjustments */
@media (max-width: 768px) {
  .resource-card {
    margin-bottom: 16px;
  }
  
  .resource-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
}

/* Loading and empty states */
.infrastructure-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 400px;
  background: #ffffff;
  border-radius: 16px;
  border: 1px solid #e8e8e8;
}

.infrastructure-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 300px;
  background: #ffffff;
  border-radius: 16px;
  border: 1px solid #e8e8e8;
  padding: 48px;
}

/* Search highlight */
.search-highlight {
  background-color: #fff3cd;
  padding: 0 2px;
  border-radius: 2px;
  color: #856404;
  font-weight: 500;
}

/* Category cards */
.category-card {
  border-radius: 16px;
  border: 1px solid #e8e8e8;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
  transition: all 0.3s ease;
}

.category-card:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
  border-color: #d9d9d9;
}

.category-header {
  padding: 4px 0;
}

/* AI Agent Reasoning Markdown Styles */
.reasoning-content {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 
               'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.6;
  color: #333;
}

.reasoning-content h1,
.reasoning-content h2,
.reasoning-content h3,
.reasoning-content h4,
.reasoning-content h5,
.reasoning-content h6 {
  margin-top: 16px;
  margin-bottom: 8px;
  font-weight: 600;
  line-height: 1.25;
  color: #1f2328;
}

.reasoning-content h1 {
  font-size: 1.5em;
  border-bottom: 1px solid #d0d7de;
  padding-bottom: 8px;
}

.reasoning-content h2 {
  font-size: 1.3em;
  border-bottom: 1px solid #d0d7de;
  padding-bottom: 6px;
}

.reasoning-content h3 {
  font-size: 1.1em;
}

.reasoning-content p {
  margin-top: 0;
  margin-bottom: 10px;
}

.reasoning-content ul,
.reasoning-content ol {
  margin-top: 0;
  margin-bottom: 10px;
  padding-left: 30px;
}

.reasoning-content li {
  margin-bottom: 4px;
}

.reasoning-content blockquote {
  margin: 0;
  padding: 0 1em;
  color: #656d76;
  border-left: 0.25em solid #d0d7de;
  background-color: #f8f9fa;
  border-radius: 0 4px 4px 0;
}

.reasoning-content code {
  padding: 0.2em 0.4em;
  margin: 0;
  font-size: 85%;
  background-color: rgba(175, 184, 193, 0.2);
  border-radius: 3px;
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
}

.reasoning-content pre {
  padding: 16px;
  overflow: auto;
  font-size: 85%;
  line-height: 1.45;
  background-color: #f6f8fa;
  border-radius: 6px;
  border: 1px solid #d0d7de;
  margin-bottom: 16px;
}

.reasoning-content pre code {
  display: inline;
  max-width: auto;
  padding: 0;
  margin: 0;
  overflow: visible;
  line-height: inherit;
  word-wrap: normal;
  background-color: transparent;
  border: 0;
}

.reasoning-content strong {
  font-weight: 600;
}

.reasoning-content em {
  font-style: italic;
}

.reasoning-content a {
  color: #0969da;
  text-decoration: none;
}

.reasoning-content a:hover {
  text-decoration: underline;
}

/* Custom Execution Plan Styles - Based on Original */
.execution-plan-card {
  margin-top: 24px;
  border: 2px solid #007bff;
  border-radius: 8px;
  box-shadow: 0 4px 12px rgba(0, 123, 255, 0.15);
}

.execution-plan-card .ant-card-head {
  background: #f8f9fa;
  border-bottom: 1px solid #007bff;
}

.execution-plan-card .ant-card-head-title {
  color: #2c3e50;
  font-weight: 600;
}

/* Enhanced Step Styling - Matching Original Design with Consistency */
.execution-step {
  background: white;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  margin-bottom: 12px;
  overflow: hidden;
  transition: all 0.3s ease;
  border-left: 4px solid #007bff;
  min-height: 100px; /* Ensure consistent height */
}

.execution-step:hover {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transform: translateY(-1px);
}

.execution-step-header {
  display: flex;
  align-items: flex-start; /* Changed to flex-start for better alignment */
  padding: 16px;
  background: #fafafa;
  min-height: 100px; /* Ensure consistent height */
}

.execution-step-number {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #007bff;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  margin-right: 15px;
  font-size: 0.9rem;
  flex-shrink: 0;
  margin-top: 2px; /* Slight adjustment for better alignment */
  position: relative;
  transition: all 0.3s ease;
}

/* Step Status Animation and Colors - Based on Old UI */
.execution-step-number.pending {
  background: #f8f9fa;
  border: 2px solid #dee2e6;
  color: #6c757d;
}

.execution-step-number.running {
  background: #007bff;
  border: 2px solid #007bff;
  color: white;
  box-shadow: 0 0 0 4px rgba(0, 123, 255, 0.2);
  animation: runningPulse 2s ease-in-out infinite;
}

.execution-step-number.completed {
  background: #28a745;
  border: 2px solid #28a745;
  color: white;
  animation: successPulse 0.6s ease-out;
}

.execution-step-number.failed {
  background: #dc3545;
  border: 2px solid #dc3545;
  color: white;
  animation: errorShake 0.6s ease-out;
}

.execution-step-number.recovery-generating {
  background: #6f42c1;
  border: 2px solid #6f42c1;
  color: white;
  animation: recoveryPulse 1.5s ease-in-out infinite;
}

.execution-step-number.recovery-pending {
  background: #ffc107;
  border: 2px solid #ffc107;
  color: #212529;
}

.execution-step-number.recovery-in-progress {
  background: #17a2b8;
  border: 2px solid #17a2b8;
  color: white;
  animation: recoveryProgress 2s ease-in-out infinite;
}

/* Status Animations - Matching Old UI */
@keyframes successPulse {
  0% { 
    transform: scale(1); 
    box-shadow: 0 0 0 0 rgba(40, 167, 69, 0.7); 
  }
  50% { 
    transform: scale(1.1); 
    box-shadow: 0 0 0 10px rgba(40, 167, 69, 0); 
  }
  100% { 
    transform: scale(1); 
    box-shadow: 0 0 0 0 rgba(40, 167, 69, 0); 
  }
}

@keyframes errorShake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-4px); }
  75% { transform: translateX(4px); }
}

@keyframes runningPulse {
  0% { 
    transform: scale(1); 
    box-shadow: 0 0 0 4px rgba(0, 123, 255, 0.2); 
  }
  50% { 
    transform: scale(1.05); 
    box-shadow: 0 0 0 8px rgba(0, 123, 255, 0.1); 
  }
  100% { 
    transform: scale(1); 
    box-shadow: 0 0 0 4px rgba(0, 123, 255, 0.2); 
  }
}

@keyframes recoveryPulse {
  0%, 100% { 
    transform: scale(1); 
    box-shadow: 0 0 0 2px rgba(111, 66, 193, 0.3); 
  }
  50% { 
    transform: scale(1.05); 
    box-shadow: 0 0 0 6px rgba(111, 66, 193, 0.1); 
  }
}

@keyframes recoveryProgress {
  0%, 100% { 
    transform: scale(1); 
    box-shadow: 0 0 0 3px rgba(23, 162, 184, 0.3); 
  }
  50% { 
    transform: scale(1.08); 
    box-shadow: 0 0 0 7px rgba(23, 162, 184, 0.1); 
  }
}

/* Execution Success Mode - Based on Old UI */
.execution-success {
  border-color: #28a745 !important;
  box-shadow: 0 4px 12px rgba(40, 167, 69, 0.15) !important;
}

.execution-success .ant-card-head {
  background: #d4edda !important;
