  background: #17a2b8;
  border: 2px solid #17a2b8;
  animation: recoveryProgress 2s ease-in-out infinite;
}

/* Multi-step recovery details styling */
.multi-step-details {
  background-color: #f8f9fa;
  border-radius: 6px;
  padding: 12px;
  margin-top: 8px;
}

.multi-step-details.expanded {
  background-color: #e9ecef;
}

.multi-step-header {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  color: #495057;
}

.toggle-steps-btn {
  background: none;
  border: none;
  cursor: pointer;
  color: #6c757d;
  font-size: 12px;
  margin-left: auto;
  padding: 4px;
  border-radius: 4px;
  transition: background-color 0.2s ease;
}

.toggle-steps-btn:hover {
  background-color: #e9ecef;
  color: #495057;
}

.multi-step-list {
  margin-top: 8px;
  padding-left: 16px;
  border-left: 2px solid #dee2e6;
}

.recovery-step-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 12px;
  padding: 8px;
  background-color: white;
  border-radius: 4px;
  border: 1px solid #e9ecef;
}

.recovery-step-item:last-child {
  margin-bottom: 0;
}

.recovery-step-item .step-number {
  min-width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #1890ff;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  font-weight: 600;
  flex-shrink: 0;
}

.recovery-step-item .step-content {
  flex: 1;
}

.step-tool {
  font-weight: 600;
  color: #1890ff;
  margin-bottom: 2px;
}

.step-purpose {
  color: #666;
  font-size: 13px;
  margin-bottom: 4px;
}

.step-params {
  font-size: 12px;
  color: #888;
}

.params-label {
  font-weight: 600;
}

.params-value {
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  background-color: #f8f9fa;
  padding: 2px 4px;
  border-radius: 3px;
  margin-left: 4px;
}

/* Recovery progress styling */
.recovery-progress-info {
  color: #666;
  font-style: italic;
  margin-top: 8px;
}

.multi-step-progress {
  margin-top: 12px;
  padding: 12px;
  background-color: #e8f4f8;
  border-radius: 6px;
  border: 1px solid #bee5eb;
}

.progress-step-info {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.progress-step-number {
  font-weight: 600;
  color: #0c5460;
}

.progress-step-purpose {
  color: #495057;
  font-size: 14px;
}

.progress-bar {
  width: 100%;
  height: 6px;
  background-color: #dee2e6;
  border-radius: 3px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background-color: #17a2b8;
  transition: width 0.3s ease;
}
